"use client";
import { useState } from "react";
import { useRouter } from "next/navigation";
import { Form } from "@/components/ui/form";

type UserRole = "owner" | "seeker";

export default function LoginPage() {
  const [isLogin, setIsLogin] = useState(true);
  const [form, setForm] = useState({
    email: "",
    password: "",
    name: "",
    mobile: "",
    role: "seeker" as UserRole,
  });
  const router = useRouter();

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    const url = isLogin ? "/api/login" : "/api/register";

    try {
      const response = await fetch(`http://localhost:3001${url}`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(form),
      });

      if (!response.ok) throw new Error("Authentication failed");

      const user = await response.json();
      localStorage.setItem("user", JSON.stringify(user));
      router.push(user.role === "owner" ? "/owner" : "/seeker");
    } catch (error) {
      console.error("Authentication error:", error);
    }
  };

  return (
    <Form onSubmit={handleSubmit}>
      <h1>{isLogin ? "Login" : "Register"}</h1>
      {!isLogin && (
        <>
          <input
            placeholder="Name"
            required
            onChange={(e) => setForm({ ...form, name: e.target.value })}
          />
          <input
            placeholder="Mobile"
            required
            onChange={(e) => setForm({ ...form, mobile: e.target.value })}
          />
        </>
      )}
      <input
        type="email"
        placeholder="Email"
        required
        onChange={(e) => setForm({ ...form, email: e.target.value })}
      />
      <input
        type="password"
        placeholder="Password"
        required
        onChange={(e) => setForm({ ...form, password: e.target.value })}
      />
      {!isLogin && (
        <select
          onChange={(e) =>
            setForm({ ...form, role: e.target.value as "owner" | "seeker" })
          }
        >
          <option value="seeker">Book Seeker</option>
          <option value="owner">Book Owner</option>
        </select>
      )}
      <button type="submit">{isLogin ? "Login" : "Register"}</button>
      <button type="button" onClick={() => setIsLogin(!isLogin)}>
        {isLogin ? "Need an account?" : "Already have an account?"}
      </button>
    </Form>
  );
}
